@import "_dark/dark-syntax";

/* the fallback color from https://raw.githubusercontent.com/jwarby/pygments-css/master/github.css */
.highlight pre { background-color: var(--highlight-pre-bg) }
.highlight .hll { background-color: var(--highlight-hll-bg,) }
.highlight .c { color: var(--highlight-c); font-style: italic } /* Comment */
.highlight .err { color: var(--highlight-err); background-color: var(--highlight-err-bg,) } /* Error */
.highlight .k { color: var(--highlight-k); font-weight: bold } /* Keyword */
.highlight .o { color: var(--highlight-o); font-weight: bold } /* Operator */
.highlight .cm { color: var(--highlight-cm); font-style: italic } /* Comment.Multiline */
.highlight .cp { color: var(--highlight-cp); font-weight: bold; font-style: italic } /* Comment.Preproc */
.highlight .c1 { color: var(--highlight-c1); font-style: italic } /* Comment.Single */
.highlight .cs { color: var(--highlight-cs); font-weight: bold; font-style: italic } /* Comment.Special */
.highlight .gd { color: var(--highlight-gd); background-color: var(#ffdddd) } /* Generic.Deleted */
.highlight .ge { color: var(--highlight-ge); font-style: italic } /* Generic.Emph */
.highlight .gr { color: var(--highlight-gr) } /* Generic.Error */
.highlight .gh { color: var(--highlight-gh) } /* Generic.Heading */
.highlight .gi { color: var(--highlight-gi); background-color: #ddffdd } /* Generic.Inserted */
.highlight .go { color: var(#888888) } /* Generic.Output */
.highlight .gp { color: var(#555555) } /* Generic.Prompt */
.highlight .gs { font-weight: bold } /* Generic.Strong */
.highlight .gu { color: var(--highlight-gu) } /* Generic.Subheading */
.highlight .gt { color: var(--highlight-gt) } /* Generic.Traceback */
.highlight .kc { color: var(--highlight-kc); font-weight: bold } /* Keyword.Constant */
.highlight .kd { color: var(--highlight-kd); font-weight: bold } /* Keyword.Declaration */
.highlight .kn { color: var(--highlight-kn); font-weight: bold } /* Keyword.Namespace */
.highlight .kp { color: var(--highlight-kp); font-weight: bold } /* Keyword.Pseudo */
.highlight .kr { color: var(--highlight-kr); font-weight: bold } /* Keyword.Reserved */
.highlight .kt { color: var(--highlight-kt); font-weight: bold } /* Keyword.Type */
.highlight .m { color: var(--highlight-m) } /* Literal.Number */
.highlight .s { color: var(--highlight-s) } /* Literal.String */
.highlight .na { color: var(--highlight-na) } /* Name.Attribute */
.highlight .nb { color: var(--highlight-nb) } /* Name.Builtin */
.highlight .nc { color: var(--highlight-nc); font-weight: bold } /* Name.Class */
.highlight .no { color: var(--highlight-no) } /* Name.Constant */
.highlight .nd { color: var(--highlight-nd); font-weight: bold } /* Name.Decorator */
.highlight .ni { color: var(--highlight-ni) } /* Name.Entity */
.highlight .ne { color: var(--highlight-ne); font-weight: bold } /* Name.Exception */
.highlight .nf { color: var(--highlight-nf); font-weight: bold } /* Name.Function */
.highlight .nl { color: var(--highlight-nl); font-weight: bold } /* Name.Label */
.highlight .nn { color: var(--highlight-nn) } /* Name.Namespace */
.highlight .nt { color: var(--highlight-nt) } /* Name.Tag */
.highlight .nv { color: var(--highlight-nv) } /* Name.Variable */
.highlight .ow { color: var(--highlight-ow); font-weight: bold } /* Operator.Word */
.highlight .w { color: var(--highlight-w) } /* Text.Whitespace */
.highlight .mf { color: var(--highlight-mf) } /* Literal.Number.Float */
.highlight .mh { color: var(--highlight-mh) } /* Literal.Number.Hex */
.highlight .mi { color: var(--highlight-mi) } /* Literal.Number.Integer */
.highlight .mo { color: var(--highlight-mo)} /* Literal.Number.Oct */
.highlight .sb { color: var(--highlight-sb) } /* Literal.String.Backtick */
.highlight .sc { color: var(--highlight-sc) } /* Literal.String.Char */
.highlight .sd { color: var(--highlight-sd) } /* Literal.String.Doc */
.highlight .s2 { color: var(--highlight-s2) } /* Literal.String.Double */
.highlight .se { color: var(--highlight-se) } /* Literal.String.Escape */
.highlight .sh { color: var(--highlight-sh) } /* Literal.String.Heredoc */
.highlight .si { color: var(--highlight-si) } /* Literal.String.Interpol */
.highlight .sx { color: var(--highlight-sx) } /* Literal.String.Other */
.highlight .sr { color: var(--highlight-sr) } /* Literal.String.Regex */
.highlight .s1 { color: var(--highlight-s1) } /* Literal.String.Single */
.highlight .ss { color: var(--highlight-ss) } /* Literal.String.Symbol */
.highlight .bp { color: var(--highlight-bp) } /* Name.Builtin.Pseudo */
.highlight .vc { color: var(--highlight-vc) } /* Name.Variable.Class */
.highlight .vg { color: var(--highlight-vg) } /* Name.Variable.Global */
.highlight .vi { color: var(--highlight-vi) } /* Name.Variable.Instance */
.highlight .il { color: var(--highlight-il) } /* Literal.Number.Integer.Long */


/*-- Codes Snippet --*/

div.highlight,
figure.highlight,
.highlighter-rouge {
  background: var(--highlight-pre-bg)
}

.highlight {
  overflow: auto;
}

.highlight .lineno {
  margin: .8rem 0rem;
  padding: 0 .5rem;
  min-width: 2.2rem;
  text-align: right;
  color: var(--highlight-lineno);
  border-right: 1px solid var(--highlight-lineno-border);
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.highlight table {
  padding: 0;
  border: 0;
}

.highlight td {
  padding: 0;
  border: 0;
}

.highlight pre {
  margin-bottom: 0;
  font-size: .85rem;
  line-height: 1.4rem;
  word-wrap: normal;  /* Fixed Safari overflow-x */
}

.highlight table td pre {
  overflow: visible;  /* Fixed iOS safari overflow-x */
  word-break: normal; /* Fixed iOS safari linenos code break */
}

/* Override BS Inline-code style */
.highlighter-rouge {
  margin-bottom: 1.2em;
}

code {
  -webkit-hyphens: none;
  -ms-hyphens: none;
  -moz-hyphens: none;
  hyphens: none;
}

code.highlighter-rouge {
  padding: 2px 4px;
  margin: 0 .3rem;
  border-radius: 4px;
}

td.rouge-code {
  padding-left: 1rem;
  padding-right: 1rem;
}

/* code snippet radius */
.highlighter-rouge,
.highlight {
  border-radius: 5px;
}

/* Hide line numbers for defualt, console, and terminal code snippets */
div[class^='highlighter-rouge'] pre.lineno,
div.language-console.highlighter-rouge pre.lineno,
div.language-terminal.highlighter-rouge pre.lineno {
  display: none;
}

div[class^='highlighter-rouge'] td.rouge-code,
div.language-console.highlighter-rouge td.rouge-code,
div.language-terminal.highlighter-rouge td.rouge-code {
  padding: .8rem 1rem;
}
